import React, {useState} from "react";
import Wrapper from "../common/core/Wrapper";
import Menu from "./Menu";
import Row from "react-bootstrap/Row";
import styles from './style.module.css';
import Header from "./Header";
import withOnImagesLoadedRender from "../common/hocs/withOnImagesLoadedRender";
import Templates from "./contents/Templates";

import avatarSrc from './images/adam.jpg';

// DEV
const user = {name: 'Adam', surname: 'Smith', avatarSrc};

const BOARD_ITEMS = new Map([
  ['Templates', Templates],
  ['Documents', null],
  ['Products', null],
  ['Services', null],
  ['My business', null],
  ['Partners', null],
  ['Competitors', null],
]);

const HEADER_HEIGHT = 60;
const LEFT_SIDE_WIDTH = 280;

function Dashboard() {
  const [tab, setTab] = useState(0);

  //
  let Tab = [...BOARD_ITEMS.entries()][tab][1];
  Tab = Tab ? Tab : () => <div></div>;

  return (
    <Wrapper className={styles.wrapper}>
      <Header height={HEADER_HEIGHT} leftSideWidth={LEFT_SIDE_WIDTH}/>
      <div className={styles.main}>
        <Menu
          user={user}
          width={LEFT_SIDE_WIDTH}
          items={BOARD_ITEMS}
          activeTabIndex={tab}
          setTab={setTab}
        />
        <div className={styles.tabContainer}>
          <Tab/>
        </div>
      </div>
    </Wrapper>
  );
}

export default withOnImagesLoadedRender(Dashboard);
